  },
  methods: {
    getValue(value){
			this.createOrg = value
    },

    //项目超期的项目显示为红色
		tableRowClassName({row, rowIndex}) {
			if (row.overdue == 1) {
				return 'red-row';
			}else{
			    return '';
			} 
		
		},
    
    // 懒加载table树形子数据
    tableLoad(row, treeNode, resolve) {
      console.log(row.batchGroupId)
      if(row.batchGroupId != "" && row.batchGroupId != undefined){
        let _params = {
          'batchGroupId':row.batchGroupId
        }
        getBatchGroupChild(_params).then(res => {
          if(res.data.successful){
            let childrenTable = res.data.resultValue

            let childrenData = this.getShowDateTable(childrenTable, this.dictMap, this.columnTypeMap)
            childrenData.forEach(function(item){
              item.isEdit = false
            })
            resolve(childrenData)
          }
        })
      }else{
        if(!row.children || row.children.length ==0){
          let params = {
            'parentItemId':row.itemId
          }
          getChildrenItem(params).then(res => {
            if(res.data.successful){
              let childrenTable = res.data.resultValue
              let childrenData = this.getShowDateTable(childrenTable, this.dictMap, this.columnTypeMap)
              childrenData.forEach(function(item){
                item.isEdit = false
              })
              this.$forceUpdate()
              resolve(childrenData)
            }
          })
        }else{
          console.log(row.children)
          let childrenData = this.getShowDateTable(row.children, this.dictMap, this.columnTypeMap)
          childrenData.forEach(function(item){
            item.isEdit = false
          })
          resolve(childrenData)
          // row.children = row.children
        }
        
      }
      
    },

    // 查看专家意见
    //展示已选专家列表
    showHasSelectedExpertDialog(data){
        let _this = this 
        _this.showHasSelectedExpert = true
        let isBatchPackage = false
        if(data.isBatchPackage ==1){
          isBatchPackage = true
        }else{
          isBatchPackage = false
        }
        getExpertsInfoWithAudit({"itemId":data.itemId,'isBatchPackage':isBatchPackage}).then(response=>{
            if(response.data.successful){
                console.log(response)
                _this.hasSelectedExpertInfoShow = response.data.resultValue
            }
        })
    },

    //上传意见文件
    uploadAdviceSuccess(response){
        // this.submitAdvice.fileId = response.data.itemValue
        let _this = this
        if(response.successful){
          _this.submitAdvice.auditFileId = response.resultValue.auditFileId 
          _this.submitAdvice.auditFileName = response.resultValue.auditFileName 
          this.$message({
            showClose: true,
            message: '上传成功',
            type: 'success'
          });
        }else{
           let str = response.resultHint
           this.$message({
            showClose: true,
            message: '上传失败 '+str,
            dangerouslyUseHTMLString: true,
            type: 'error'
          }); 
        }
    },

    //监听表单勾选事件
		handleSelectionChange(val) {
			this.uploadArr = []
			this.uploadArr = this.uploadArr.concat(val);
			
		},

    // 批量上传正式评审文件打开弹窗
    uploadFileList() {
      let _this = this
      if(_this.uploadArr.length==0){
				this.$message({
					showClose: true,
					message: '请至少选择一个项目',
					type:'warning'
				})
				return false
      }
      _this.fileItemIds = []
      _this.fileIsBatch = []
      console.log(_this.uploadArr)
      for(let i=0;i<_this.uploadArr.length;i++){
				
        _this.fileItemPhaseDet = _this.uploadArr[i].itemPhaseDet
        let isBatch = false
        if(_this.uploadArr[i].isBatchPackage ==1){
          isBatch = true
        }else{
          isBatch = false
        }
        _this.fileItemIds.push(_this.uploadArr[i].itemId)
        _this.fileIsBatch.push(isBatch)
				if(_this.fileItemPhaseDet!='20'){
          this.$message({
						showClose: true,
						message: '所选项目包含状态不符合业务逻辑的项目,请重新选择',
						type:'warning'
					})
					return false
				}	
      }
      this.dialogFileListVisible = true
      _this.uploadList.auditFileNo = ""
      _this.fileList4 = []
      _this.fileTypeList =[]
      _this.fileType = ""
      _this.finalFileData1 = {"itemIds":_this.fileItemIds,"itemPhaseDet":_this.fileItemPhaseDet,"auditFileNo":_this.uploadList.auditFileNo,
                            'isBatchPackages':_this.fileIsBatch}
      console.log(_this.finalFileData1)
    },

    // 点击批量上传弹窗里的确认
    submitFileList(){
      let params = {
            "user": this.$route.query.iscUserId,
            "workItemID": this.$route.query.workItemID
        }
        this.loadingBtn1 = true
        if(!this.uploadList.auditFileNo){
          this.$message({
            showClose: true,
            message: '请输入项目可研评审文号',
            type: 'warning'
          });
          return false
        }
        if(this.fileTypeList =="" || this.fileTypeList == undefined){
          this.$message({
            showClose: true,
            message: '请上传格式为ceb或者pdf的文件',
            type: 'warning'
          });
          return false
        }
        for(let i=0; i<this.fileTypeList.length; i++){
          if(this.fileTypeList[i] != "ceb" && this.fileTypeList[i] != "pdf" && this.fileTypeList[i] != "CEB" && this.fileTypeList[i] != "PDF"){
            this.$message({
              showClose: true,
              message: '只能上传格式为ceb或者pdf的文件',
              type: 'warning'
            });
            return false
          }
        }
        
        this.finalFileData1.auditFileNo = this.uploadList.auditFileNo
        this.$refs.uploadFileListSubmit.submit() 
    },

    // 上传成功之后调用
    uploadSuccess2(response){
      console.log(response)
      debugger
       let _this = this
        if(response.successful){
           _this.$message({
                  showClose: true,
                  message: '上传正式评审文件成功',
                  type: 'success'
                });
            _this.dialogFileVisible = false
            _this.dialogFileListVisible = false
            _this.loadingBtn1 = false
            _this.getTable(_this.currentPage,_this.currentNum)
            // _this.$message.success("操作成功")
            console.log(response.resultValue)
              //上传正式评审文件之后调用通知流程  add by gao.feng
            let noticeArr = []
            let noticeData = {}
            noticeData.content = response.resultValue.itemName + '项目，已经上传正式评审文件，评审文号 ' + response.resultValue.auditFileNo
            noticeData.noticeUser = response.resultValue.createor;
            noticeArr.push(noticeData)//流程通知所需参数
            noticeAdd(noticeArr).then(response =>{
                console.log(response)
            })
            

            //上传正式评审文件之后调用自动编码
            //获取调用国电通编码器所使用的参数 add by gao.feng
            let itemId = _this.finalFileData.itemId
            let thisParam ={
              'itemId':itemId,
              'isBatchPackage':_this.tempIsBatch
            }
            getChildIdList(thisParam).then(res => {
              if(res.data.successful){
                let childTableList = res.data.resultValue
                for(let i=0;i<childTableList.length;i++){
                    let tempData = childTableList[i]
                    console.log("调用国电通编码器")
                      doPostApplyItemCode({"itemId":tempData.itemId}).then(response=>{
                      if(response.data.successful){
                        let param = response.data.resultValue;
                        console.log(param)
                        getBM(param).then(res=>{
                            console.log(res)
                            let status = res.data.status
                            if(status.indexOf("200")!=-1){//调用失败
                                // _this.$message.error("调用编码器失败,权限校验失败")
                                console.log("调用编码器失败,权限校验失败")
                            }else if(status.indexOf("100")!=-1){
                                let applyResultList = res.data.applyResult
                                let result = applyResultList[0]
                                if(result.status.indexOf("100")!=-1){//调用成功
                                      let baseCode = result.baseCode
                                      //更新项目编码
                                      updateItemCode({"itemId":tempData.itemId,"code":baseCode}).then(res1=>{
                                            if(res1.data.successful){
                                                _this.$message.success("编码成功")
                                            }else{
                                                _this.$message.error('编码失败')
                                            }
                                      })
                                  }else if(result.status.indexOf("200")!=-1){
                                      let errormsg = ''
                                      for(let i in result.applyResult){
                                          errormsg = result.applyResult[i]
                                      }
                                      // _this.$message.error(errormsg)
                                      console.log(errormsg)
                                  }
                            }
                          })
                      }
                    })
                }
              }
            })
        }else{
            _this.$message.error("上传失败")
        }
    },

    // 上传正式评审文件点击确认
