    },

    //显示操作弹框--提交现场评审意见
    showDialog(data){
      let _this =this
      if(data.isBatchPackage == 1){
        _this.tempIsBatch = true
      }else{
        _this.tempIsBatch = false
      }
      if(data.itemPhaseDet == 18){
        // 体检现场评审文件
        _this.dialogLoading =true
        _this.itemSpeciality = data.speciality
        _this.dialogFormVisible = true
        _this.fileList2 = []
        _this.submitAdvice= {
          "checkAdvice":"",
          "auditFileId":"",
          "auditFileName":"",
          "agreeType":"1",
          "itemId":"",
          "isBatchPackage":_this.tempIsBatch
        }
        _this.submitAdvice.itemId = data.itemId
      }else if(data.itemPhaseDet ==20){
        _this.dialogFileVisible = true
        _this.auditFileNo = ""
        _this.fileList3 = []
        _this.fileTypeList =[]
        _this.fileType = ""
        _this.finalFileData = {"itemId":data.itemId,"itemPhaseDet":data.itemPhaseDet,"auditFileNo":_this.auditFileNo,
                              'isBatchPackage':_this.tempIsBatch}
      }
     
    },

    //提交现场评审意见
    submitAdviceXC(){
        let _this = this
        _this.submitXCLoading = true
        submitOffLineAdvice(_this.submitAdvice).then(res=>{
          _this.submitXCLoading = false
            if(res.data.successful){
                _this.$message({
                  showClose: true,
                  message: '提交现场评审意见成功',
                  type: 'success'
                });
                _this.dialogFormVisible = false
                _this.getTable(_this.currentPage,_this.currentNum)
                
            }else{
                _this.$message({
                  showClose: true,
                  message: '提交现场评审意见失败',
                  type: 'error'
                });
            }
        })
    },
    //展示项目超期原因
    projectOverdue(data){
        let _this = this 
        _this.isLeader = false
        let param = {
          itemId:data.itemId,
          isBatchPackage:data.isBatchPackage
        }
        isItemLeader(param).then(res => {
          if(res.data.successful){
            if(res.data.resultValue){
              _this.isLeader = true
            }else{
              _this.isLeader = false
            }
          }
        })

        getOverDueReason(param).then(res => {
          if(res.data.successful){
            _this.overdueReason = res.data.resultValue
          }
        })
        _this.showOverDueDialog = true
        _this.overdueReason = ""
        _this.tempItemId = data.itemId
        _this.isBatchPackage = data.isBatchPackage
    },
    // 提交超期原因
    confirmOverdue(){
      let param = {
        itemId:this.tempItemId,
        isBatchPackage:this.isBatchPackage,
        reason:this.overdueReason
      }
      saveOverDueReason(param).then(res => {
        if(res.data.successful){
          this.showOverDueDialog = false
          this.getTable(this.currentPage,this.currentNum)
        }
      }) 
    },
    //获取查询条件下拉框
		getItemLabelSearch() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue
          // 专业类别
          _this.selectParamDict.set('speciality', labelList.specialityList)
          _this.$forceUpdate()
				}
			})
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
    },
    // 搜索框变化重新渲染表格
    selectItem(){
      this.currentPage = 1;
      this.getTable(1,this.currentNum)
    },
    
    // 子组件向父组件传递参数
    changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
		},
    //展示项目信息详情
    showItemInfo(data){
      
      this.showData.itemId = data.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
    },
     // 页面主表格获取当前每页条数
     handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.tableDataLoding = true
      _this.param.index = page
      _this.param.pageSize = size
      _this.param.itemPhaseDet = "11,12,18"
      expertsQueryTitle().then(response => {
        if(response.data.successful){
            _this.tableLabel = response.data.resultValue
            let arr = response.data.resultValue
            _this.tableLabel = _this.getLabelList(arr)
            _this.dictMap = _this.getDictArr(arr)
            _this.columnTypeMap = _this.getColumnTypeArr(arr)
            getItemList(_this.param).then(response => {//{}写需要的参数
            console.log('response',response)//后端返回数据
            if(response.data.successful){
                _this.totalNum = response.data.resultValue.totalNum
                _this.tableData = response.data.resultValue.tableData
                _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)

            }
            _this.tableDataLoding = false
            
          }).catch(error => {
            console.log(error)//error
            _this.tableDataLoding = false
          })
        }
      })
      
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(key=="itemPhaseDet"){
               copyObj["itemPhaseDetT"] = value;
          }
          if(key=="speciality"){
              copyObj["specialityT"] = value;
          }
           
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="date" && value){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }

          
        }
        
        arr.push(copyObj);
        console.log(arr)
      }
      return arr;
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
     //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    }
  }
}
</script>
